import React, {useState,useEffect} from 'react'
import axios from 'axios';
import {useParams,useHistory} from 'react-router-dom';
import New from './New';
import {Link} from 'react-router-dom'

function Edit() {
   let history=useHistory();
  const {id}=useParams();


  const [user, setUser] = useState({
    name:"",
    phone:"",
    email:""
  });
  const {name,phone,email}=user;

  const onSubmit= async(e)=>{
    e.preventDefault();
    await axios.put(`http://localhost:3005/user/${id}`,user);
    history.push('/Thank');
    
  }


  useEffect(()=>{
    const loadUser=async()=>{
      const results=await axios.get(`http://localhost:3005/user/${id}`);
      setUser(results.data);
    }
    loadUser();
  },[id]);

 
  
  const onInputChange=e=>{
    setUser({...user,[e.target.name]:e.target.value});
    };
  return (
    <><New/>
    <div className="aa">
    <div className="adduser">
    <div className="aad"><h5>UPDATE USER DATA</h5></div>
    <form onSubmit={e=>onSubmit(e)}>

<div className="form-group">
  <label for="name">Name:</label>
  <input type="text" className="form-control" placeholder='Enter your Name' name="name" required value={name} onChange={e =>onInputChange(e)}/>
</div>
<div className="form-group">
  <label for="phone">Mobile No:</label>
  <input type="tel"  required pattern='[0-9]{10}' className="form-control" placeholder='Enter Phone No.' name="phone" value={phone} onChange={e =>onInputChange(e)}/>
</div>
<div className="form-group">
  <label for="email">Email:</label>
  <input type="email" className="form-control" placeholder='Enter Email ID' name="email" value={email} onChange={e =>onInputChange(e)}/>
</div>
<div className="btncls">
 <button type='submit' className="btn btn-sm m-3 btn-success">Update</button>
 <Link className="btn btn-sm m-3 btn-danger" to="/home">Back</Link>
 </div>
 <br/>
   </form>
   </div> </div>


    </>
  )
}

export default Edit;