import React,{useState,useEffect} from 'react'
import axios from 'axios';
import {Link} from 'react-router-dom';
import New from './New';

const Home=()=> {
  const [users, setUser] = useState([]);
  
  useEffect(()=>{
    const getsAll=async()=>{
      try{
        const results=await axios.get("http://localhost:3005/user");
        setUser(results.data.reverse())
      }
      catch(error){
console.log("something is wrong");
      }
    }
    getsAll();
  },[]);

 
  const deleteUser= async id=>{
   await axios.delete(`http://localhost:3005/user/${id}`);
    const newData= users.filter((item)=>{
      return item.id !==id;
    })
 
    setUser(newData);
  
  }
  return (
    <><New/>
    <div className="backtb">
    <table className="border-dark table bgsm">

     
  <thead className='abcd'>
    <tr>
      <th scope="col">S.No</th>
      <th scope="col">VIEW DATA</th>
      <th scope="col">NAME</th>
      <th scope="col">PHONE NO</th>
      <th scope="col">EMAIL</th>
      <th scope="col">ACTION</th>
    </tr>
  </thead>
  <tbody>
    {
      users.map((user,index)=>(
        <tr key={index}>
        <th scope="row">{index+1}</th>
        <td><Link className='btn btn-outline-primary' to={`/View/${user.id}`}>VIEW</Link></td>
        <td>{user.name}</td>
        <td>{user.phone}</td>
        <td>{user.email}</td>
        <td>
        
        <Link className='btn btn-success' to={`/Edit/${user.id}`}>EDIT</Link>
        <Link className='btn btn-danger' onClick={()=>deleteUser(user.id)}>DELETE</Link>
        </td>
      </tr>
      ))
    }
   
  </tbody>
</table>
    
    </div>
    </>
  )
}

export default Home