import React from 'react';
import {NavLink} from 'react-router-dom';

function Navbar() {
  return (
    <>
         
<nav className="navbar navbar-expand-sm navbar-dark bg-success">
  <div className="container-fluid btnn">
    <NavLink className="navbar-brand" to="/">CRUD APPLICATION</NavLink>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#mynavbar">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse btnn" id="mynavbar">
      <ul className="navbar-nav me-4">
        <li className="nav-item">
          <NavLink className="nav-link" to="/home">Home</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to="/About">About</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to="/Services">Services</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to="/Contact">Contact</NavLink>
        </li>
      </ul>
      <form className="d-flex">
        <input className="form-control me-2" type="text" placeholder="Search"/>
        <button className="btn btn-danger" type="button">Search</button>
        
      </form>
      <NavLink className="btn btn321 btn-outline-light btn-bg" to="/Adduser">Add User</NavLink> 
    </div>
  </div>
</nav>
    
    </>
  )
}

export default Navbar